<template>
  <div>
    <v-row>
      <v-col>
        <v-card ><img :src = 'picurl' alt="Please give your data."/></v-card>
      </v-col>
      <v-col>
        <v-card elevation="6" >
    <v-card-title>Start Now!</v-card-title>
    <v-card-subtitle>If you have any doubt, go to our welcome page for help.</v-card-subtitle>
    <v-card-text>
    <v-form
      ref="form"
    >
      <v-text-field
        v-model="name"
        :rules="nameRules"
        label=" Center."
