<script>
import axios from 'axios';
export default {
name: 'Use1',
data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Longitude is required',
      ],
      email: '',
      emailRules: [
        v => !!v || 'Latitude is required',
      ],
      time:'5',
      timeRules:[
        v => !!v || 'Spin time is required',
        v => v>=5 || 'Done\'t set the spin time too short'
      ],
      select: null,
      items: [
        'Amount.',
        'Landing.',
        'Taking off.',
        'All displayed.',
      ],
      checkbox: false,
      lazy: false,
      picurl: ''
    }),
methods: {
      validate () {
        var that = this;
        console.log(this.$data.name);
        console.log(123);
        this.$refs.form.validate();
        const path = 'http://0.0.0.0:5000/';
      axios({ method: 'POST',
              url: path,
              data: {
              'Center': this.$data.name,
              'Northeast': this.$data.email,
              'time': this.$data.time,
              'Item': this.$data.select
            },
            headers:{
              "Access-Control-Allow-Origin" : '*',
              "Access-Control-Allow-Methods" : 'POST,GET,OPTIONS',
              'Access-Control-Allow-Headers': 'X-PINGOTHER, Content-Type',
              'Access-Control-Max-Age': '86400'
            },
            responseType: "arraybuffer"
            
          }).then(function(response) {
            console.log(response)
            that.picurl =
          "data:image/jpeg;base64," + that.arrayBufferToBase64(response.data);
      })
      },
    // ArrayBuffer转为base64字符串
    arrayBufferToBase64(buffer) {
      //第一步，将ArrayBuffer转为二进制字符串
      var binary = "";
      var bytes = new Uint8Array(buffer);
      var len = bytes.byteLength;
      for (var i = 0; i < len; i++) {
        binary += String.fromCharCode(bytes[i]);
      }
      //将二进制字符串转为base64字符串
      return window.btoa(binary);
    },
          reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      }
      }
      

